<template>
	<view class="container">
		<!-- ÂØºËà™ËÆ∞ÂΩïÂàóË°® -->
		<scroll-view class="record-list" scroll-y>
			<view v-if="records.length === 0" class="empty-tip">
				<text>ÊöÇÊó†ÂØºËà™ËÆ∞ÂΩï</text>
			</view>
			
			<view v-for="(record, index) in records" :key="index" class="record-item" @tap="viewRecordDetails(record)">
					<view class="record-header">
						<text class="record-time">{{ formatDate(record.data.startTime) }}</text>
						<text class="record-mode">{{ getModeName(record.data.mode) }}</text>
					</view>
					
					<!-- Ë∑ØÁ∫øÈùôÊÄÅÂú∞Âõæ -->
					<view class="record-map" :class="{ 'map-loaded': staticMapUrls[index] }">
						<image v-if="staticMapUrls[index]" :src="staticMapUrls[index]" mode="aspectFill" class="map-image"></image>
						<view v-else class="map-loading">
							<text>Âú∞ÂõæÂä†ËΩΩ‰∏≠...</text>
						</view>
					</view>
					
					<view class="record-route">
						<view class="route-point start">
							<text class="point-icon">üìç</text>
							<text class="point-name">{{ record.data.startPoint.name || 'ÂΩìÂâç‰ΩçÁΩÆ' }}</text>
						</view>
						<view class="route-line"></view>
						<view class="route-point end">
							<text class="point-icon">üèÅ</text>
							<text class="point-name">{{ record.data.endPoint.name }}</text>
						</view>
					</view>
					<view class="record-info">
						<text class="record-duration">ÊåÅÁª≠Êó∂Èó¥: {{ formatDuration(record.data.duration) }}</text>
						<text class="record-points">ÈÄîÁªèÁÇπ: {{ record.data.points.length }} ‰∏™</text>
					</view>
				</view>
		</scroll-view>
	</view>
</template>

<script>
		// ÂºïÂÖ•È´òÂæ∑Âú∞ÂõæSDK
		const { AMapWX } = require('../../libs/amap-wx.130.js')
		export default {
			data() {
				return {
					records: [], // ÂØºËà™ËÆ∞ÂΩïÂàóË°®
					mapKey: '870dfd4461a0542da7d89cbf995185ca', // Âú∞ÂõæAPIÂØÜÈí•ÔºàËØ∑ÊõøÊç¢‰∏∫ÊúâÊïàÁöÑÂØÜÈí•Ôºâ
					staticMapUrls: {} // Â≠òÂÇ®ÊØè‰∏™ËÆ∞ÂΩïÁöÑÈùôÊÄÅÂú∞ÂõæURL
				}
			},
		
		onLoad() {
			this.fetchNavigationRecords(); // Âä†ËΩΩÈ°µÈù¢Êó∂Ëé∑ÂèñÂØºËà™ËÆ∞ÂΩï
		},
		
		onShow() {
				// È°µÈù¢ÊòæÁ§∫Êó∂‰πüËé∑Âèñ‰∏ÄÊ¨°ÔºåÁ°Æ‰øùÊï∞ÊçÆÊòØÊúÄÊñ∞ÁöÑ
				this.fetchNavigationRecords();
			},
		
		methods: {
			// ‰ªéMongoDBËé∑ÂèñÂØºËà™ËÆ∞ÂΩï
			fetchNavigationRecords() {
				uni.request({
					url: 'http://localhost:3000/api/getMapData',
					method: 'GET',
					success: (res) => {
						console.log('Ëé∑ÂèñÂà∞ÁöÑÂØºËà™ËÆ∞ÂΩïÊï∞ÊçÆ:', res.data);
						// Á≠õÈÄâÂá∫Á±ªÂûã‰∏∫navigation_recordÁöÑÊï∞ÊçÆ
						if (res.data && res.data.data) {
							this.records = res.data.data
								.filter(item => item.type === 'navigation_record')
								.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt)); // ÊåâÊó∂Èó¥ÂÄíÂ∫èÊéíÂàó
						} else {
							// ÂÖºÂÆπÊóßÁöÑÂìçÂ∫îÊ†ºÂºè
							this.records = res.data
								.filter(item => item.type === 'navigation_record')
								.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
						}
						
						// ËÆ∞ÂΩïÂàóË°®Êõ¥Êñ∞ÂêéÔºå‰∏∫ÊØè‰∏™ËÆ∞ÂΩïËé∑ÂèñÈùôÊÄÅÂú∞Âõæ
						this.$nextTick(() => {
							this.records.forEach((record, index) => {
								this.getStaticMapUrl(record, index);
							});
						});
					},
					fail: (err) => {
						console.error('Ëé∑ÂèñÂØºËà™ËÆ∞ÂΩïÂ§±Ë¥•:', err);
						uni.showToast({
							title: 'Ëé∑ÂèñËÆ∞ÂΩïÂ§±Ë¥•',
							icon: 'none'
						});
					}
				});
			},
			
			// Ëé∑ÂèñÈùôÊÄÅÂú∞ÂõæURL
			getStaticMapUrl(record, index) {
				// Â¶ÇÊûúÂ∑≤ÁªèËé∑ÂèñËøáËØ•ËÆ∞ÂΩïÁöÑÈùôÊÄÅÂú∞ÂõæÔºåÂàôÁõ¥Êé•ËøîÂõû
				if (this.staticMapUrls[index]) {
					return this.staticMapUrls[index];
				}
				
				const amapwx = new AMapWX({key: this.mapKey});
				const { startPoint, endPoint, points } = record.data;
				
				// ÊûÑÂª∫Ë∑ØÂæÑÂèÇÊï∞
				let pathPoints = '';
				if (points && points.length > 0) {
					// ‰ªéÈÄîÁªèÁÇπÊûÑÂª∫Ë∑ØÂæÑ
					pathPoints = points.map(p => `${p.longitude},${p.latitude}`).join(';');
				} else if (startPoint && endPoint && startPoint.latitude && endPoint.latitude) {
					// Â¶ÇÊûúÊ≤°ÊúâÈÄîÁªèÁÇπÔºå‰ΩøÁî®Ëµ∑ÁÇπÂíåÁªàÁÇπ
					pathPoints = `${startPoint.longitude},${startPoint.latitude};${endPoint.longitude},${endPoint.latitude}`;
				}
				
				// ËÆæÁΩÆ‰∏≠ÂøÉÁÇπÂíåÁº©ÊîæÁ∫ßÂà´
				let center = '';
				if (startPoint && startPoint.latitude) {
					center = `${startPoint.longitude},${startPoint.latitude}`;
				} else if (endPoint && endPoint.latitude) {
					// Â¶ÇÊûúÊ≤°ÊúâËµ∑ÁÇπÔºå‰ΩøÁî®ÁªàÁÇπ‰Ωú‰∏∫‰∏≠ÂøÉÁÇπ
					center = `${endPoint.longitude},${endPoint.latitude}`;
				} else if (points && points.length > 0) {
					// Â¶ÇÊûúÊ≤°ÊúâËµ∑ÁÇπÂíåÁªàÁÇπÔºå‰ΩÜÊúâÈÄîÁªèÁÇπÔºå‰ΩøÁî®Á¨¨‰∏Ä‰∏™ÈÄîÁªèÁÇπ‰Ωú‰∏∫‰∏≠ÂøÉÁÇπ
					center = `${points[0].longitude},${points[0].latitude}`;
				}
				
				// ÊûÑÂª∫Ê†áËÆ∞ÁÇπÂèÇÊï∞
				let markers = [];
				if (startPoint && startPoint.latitude) {
					markers.push(`mid,0xFF0000,0:${startPoint.longitude},${startPoint.latitude}`); // Ëµ∑ÁÇπÊ†áËÆ∞ÔºàÁ∫¢Ëâ≤Ôºâ
				}
				if (endPoint && endPoint.latitude) {
					markers.push(`mid,0x0091ff,1:${endPoint.longitude},${endPoint.latitude}`); // ÁªàÁÇπÊ†áËÆ∞ÔºàËìùËâ≤Ôºâ
				}
				
				// Ë∞ÉÁî®getStaticmapÊñπÊ≥ïËé∑ÂèñÈùôÊÄÅÂú∞ÂõæURL
				console.log('ÂáÜÂ§áËé∑ÂèñÈùôÊÄÅÂú∞ÂõæÔºå‰∏≠ÂøÉÁÇπ:', center);
				console.log('APIÂØÜÈí•:', this.mapKey);
				
				// Á°Æ‰øùÊúâÊúâÊïàÁöÑ‰∏≠ÂøÉÁÇπ
				if (!center) {
					console.error('ÈùôÊÄÅÂú∞ÂõæËé∑ÂèñÂ§±Ë¥•: Ê≤°ÊúâÊúâÊïàÁöÑ‰∏≠ÂøÉÁÇπÂùêÊ†á');
					// ËÆæÁΩÆ‰∏Ä‰∏™ÈªòËÆ§‰∏≠ÂøÉÁÇπÔºàÂåó‰∫¨Â∏Ç‰∏≠ÂøÉÂùêÊ†áÔºâ
					center = '116.405285,39.904989';
					console.log('‰ΩøÁî®ÈªòËÆ§‰∏≠ÂøÉÁÇπ:', center);
				}
				
				amapwx.getStaticmap({
					location: center,
					zoom: 12,
					size: '300*200',
					scale: 2,
					markers: markers.join('|'),
					paths: pathPoints ? `0x0091ff,5,,:${pathPoints}` : '',
					success: (res) => {
						console.log('ÈùôÊÄÅÂú∞ÂõæURLËé∑ÂèñÊàêÂäü:', res.url);
						this.$set(this.staticMapUrls, index, res.url);
					},
					fail: (err) => {
						console.error('ÈùôÊÄÅÂú∞ÂõæURLËé∑ÂèñÂ§±Ë¥•:', err);
						// ÈîôËØØÁ†Å20003ÈÄöÂ∏∏Ë°®Á§∫KEYÊó†ÊïàÊàñÊ†ºÂºèÈîôËØØ
						if (err && err.errCode === '20003') {
							console.error('APIÂØÜÈí•Êó†ÊïàÊàñÂ∑≤ËøáÊúüÔºåËØ∑Ê£ÄÊü•mapKeyËÆæÁΩÆ');
							// ËøôÈáåÂèØ‰ª•Ê∑ªÂä†‰∏Ä‰∏™Ê®°ÊãüÁöÑÂú∞ÂõæURL‰Ωú‰∏∫Â§áÁî®
							const mockMapUrl = 'https://a.amap.com/jsapi/static/image?zoom=12&size=300x200&center=' + center;
							this.$set(this.staticMapUrls, index, mockMapUrl);
							console.log('‰ΩøÁî®Ê®°ÊãüÂú∞ÂõæURL:', mockMapUrl);
						}
					}
				});
				
				// ËøîÂõû‰∏Ä‰∏™Âç†‰ΩçÁ¨¶ÔºåÂÆûÈôÖURL‰ºöÂú®Ëé∑ÂèñÊàêÂäüÂêéÊõ¥Êñ∞
				return '';
			},
			
			// Êü•ÁúãËÆ∞ÂΩïËØ¶ÊÉÖ
			viewRecordDetails(record) {
				// ËøôÈáåÂèØ‰ª•Ë∑≥ËΩ¨Âà∞ËØ¶ÊÉÖÈ°µÈù¢ÔºåÊàñËÄÖÂú®ÂΩìÂâçÈ°µÈù¢ÂºπÂá∫ËØ¶ÊÉÖ
				console.log('Êü•ÁúãËÆ∞ÂΩïËØ¶ÊÉÖ:', record);
				uni.showModal({
					title: 'ÂØºËà™ËØ¶ÊÉÖ',
					content: `
						ÂºÄÂßãÊó∂Èó¥: ${this.formatDate(record.data.startTime)}
						ÁªìÊùüÊó∂Èó¥: ${this.formatDate(record.data.endTime)}
						ÊåÅÁª≠Êó∂Èó¥: ${this.formatDuration(record.data.duration)}
						Âá∫Ë°åÊñπÂºè: ${this.getModeName(record.data.mode)}
						ÈÄîÁªèÁÇπÊï∞Èáè: ${record.data.points.length} ‰∏™
					`,
					showCancel: false
				});
			},
			
			// Ê†ºÂºèÂåñÊó•Êúü
			formatDate(dateString) {
				if (!dateString) return '';
				const date = new Date(dateString);
				return `${date.getFullYear()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`;
			},
			
			// Ê†ºÂºèÂåñÊåÅÁª≠Êó∂Èó¥
			formatDuration(seconds) {
				if (!seconds) return '0ÂàÜÈíü';
				const hours = Math.floor(seconds / 3600);
				const minutes = Math.floor((seconds % 3600) / 60);
				if (hours > 0) {
					return `${hours}Â∞èÊó∂${minutes}ÂàÜÈíü`;
				} else {
					return `${minutes}ÂàÜÈíü`;
				}
			},
			
			// Ëé∑ÂèñÂá∫Ë°åÊñπÂºèÂêçÁß∞
			getModeName(mode) {
				const modeMap = {
					'driving': 'È©æËΩ¶',
					'walking': 'Ê≠•Ë°å'
				};
				return modeMap[mode] || mode;
			}
		}
	}
</script>

