	}
	
	/* Âú∞ÂõæÂä†ËΩΩÂÆåÊàêÂêéÁöÑÊ†∑ÂºèÂèòÂåñ */
	.map-loaded {
		background-color: transparent;
	}
	.record-header {
		display: flex;
